  0% {
    box-shadow: 0 0 0 0 rgba(93, 93, 93, 0.7); }
  65% {
    box-shadow: 0 0 0 0.45em rgba(93, 93, 93, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(93, 93, 93, 0); } }
  .lnbRadiate:hover, .lnbRadiate--white:hover {
    box-shadow: 0 0 0 0.3em rgba(93, 93, 93, 0.2) !important;
    transition: 0.2s; }
  .lnbRadiate--white {
    animation: radiate-white 3s infinite; }

